.step-indicator {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.step-indicator__circle {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: transparent;
  border: 1px solid #4D00EF;
  cursor: not-allowed;  // Par défaut, les cercles ne sont pas cliquables
}

.step-indicator__circle--before-active { // Seulement les cercles avant le cercle actif ont le pointeur de souris
  cursor: pointer;
}

.step-indicator__circle--before-active:hover { // Seulement les cercles avant le cercle actif changent de couleur au hover
  background: white;
  border: 1px solid #4D00EF;
}

.step-indicator__circle--active {
  background: #4D00EF;
}
