.Signup {
  width: 100%;
  height: 100vh;
  // margin-top: 100px;
  position: absolute;
  left: 0;
  display: flex;
  justify-content: center;
  // align-items: center;
  // top: 0;
}

.logo {
  position: absolute;
  left: 20px;
  top: 30px;
  display: flex;
  flex-direction: column;
  gap: 5px;
  justify-content: center;
  align-items: center;

  img {
    width: 70px;
    height: 70px;
  }
  
  h4 {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 14px;
    color: #caced3;
  }

}

.login-container {
  position: relative; // ajouté pour positionner le pseudo-élément
  border: none;
  background: transparent;
  box-shadow: 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  
  // Ajouté le pseudo-élément pour l'image de fond avec opacité
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: url("./../Images/ia.png");
    background-repeat: no-repeat;
    background-size: 50% 100%;
    background-position: center; // Centrer l'image
    opacity: 0.1; // Baisser l'opacité
    z-index: -1; // Mettre en arrière-plan
  }

    
  h1 {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: clamp(40px, 5%, 55px);
    line-height: normal;
    color: #FFFFFF;
  }

  img {
    width: 15%;
  }

  a {
    box-shadow: 0 12px 15px 0 rgba(0, 0, 0, .24), 0 17px 50px 0 rgba(0, 0, 0, .19);
    background: #7C2EFB;
    border-radius: 15px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: normal;
    color: #FFFFFF;
    padding: 10px 55px;
    cursor: pointer;
    transition: 0.5s all;
  }
  a:hover {
      background: transparent;
      border: 1px solid #7C2EFB;
      color: #7C2EFB;
      transform: scale(1.05);
  }
}