.container-user-info {
    width: 100vw;
    height: 100vh;
    display: flex;
    position: relative;
    left: -100px;

    .select-custom {
      background-color: transparent !important;
      border: 0;
      font-size: 14px;
      height: 50px;
      font-family: 'Roboto Regular';
      font-weight: 100;
      width: 100%;
      border-radius: 0;
      color: white !important;
      border-bottom: 1px solid #4D00EF;
  }

  .select-custom-focus {
      background-color: transparent !important;
      position: relative;
      border: 0;
      color: white !important;
      height: 50px;
      font-size: 14px;
      font-family: 'Roboto Regular';
      font-weight: 100;
      width: 100%;
      // border: 1px solid #9A4AFF !important;
  }
    
    .form-user-info {
        border: 1px solid #4D00EF;
        display: flex;
        flex-direction: column;
        height: 100%;
        // flex-wrap: wrap;
        width: 100%;
        border-radius: 10px;
        // min-height: 400px;


        .container-step {
            display: flex;
            flex-direction: column;
            gap: 2rem;
            width: 100%;
            height: 100%;
            border-radius: 10px;
            padding-bottom: 20px;
            
            .box-img {
                width: 100%;
                height: 40%;

                img {
                    border-radius: 10px;
                    width: 100%;
                    // height: 150px;
                    height: 100%;
                    aspect-ratio: 3/2;
                    object-fit: cover;
                    object-position: 10% 45%;
                }
            }

            .container-info {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                width: 100%;
                position: relative;
                gap: 30px;
                // height: 70%;
                flex-wrap: wrap;

                .stats-back {
                  position: absolute;
                  top: 0px;
                  left: 20px;
                  cursor: pointer;
                  span {
                      font-family: 'Plus Jakarta Sans';
                      font-style: normal;
                      font-weight: 700;
                      font-size: 12px;
                      line-height: normal;
                      color: #FFFFFF;
                      display: flex;
                      align-items: center;
                      gap: 5px;
                  }
                }

                .box-info {
                    width: 30%;
                    display: flex;
                    flex-direction: column;
                    gap: 7px;

                    h4 {
                        font-family: 'Plus Jakarta Sans';
                        font-style: normal;
                        font-weight: 700;
                        font-size: 16px;
                        line-height: 30px;
                        text-align: center;
                        /* identical to box height, or 188% */
        
                        letter-spacing: 0.02em;
        
                        /* BG */
        
                        color: #FFFFFF;
                    }
        
                    input {
                        height: 40px;
                        background: transparent;
                        border: 0;
                        border-bottom: 1px solid #4D00EF;
                        // border-radius: 10px;
                        font-family: 'Roboto';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 16px;
                        padding: 20px;
                        color: #E7E7E7;
                    }
                    input::placeholder {
                        color: #918f8f;
                    }
                    input:focus {
                        outline: none;
                        border: none;
                        border-bottom: 1px solid #4D00EF;
                    }
                }
                .box-logo {
                    width: 50%;
                    display: flex;
                    flex-direction: column;
                    gap: 15px;
                    justify-content: center;
                    align-items: center;
        
                    label {
                        font-family: 'Plus Jakarta Sans';
                        font-style: italic;
                        font-weight: 700;
                        font-size: 16px;
                        line-height: 30px;
                        /* identical to box height, or 188% */
        
                        letter-spacing: 0.02em;
        
                        /* BG */
        
                        color: #FFFFFF;
                    }
                }
                
                .box-btn {
                  width: 100%;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  padding-top: 20px;
                
                  button {
                    padding: 10px 90px;
                    background: #4D00EF;
                    border-radius: 38px;
                    border: 1px solid #4D00EF;
                    color: #FFFFFF;
                    font-family: 'Roboto';
                    font-style: normal;
                    font-weight: 300;
                    font-size: 20px;
                    line-height: 22px;
                    cursor: pointer;
                    /* Ajout pour l'animation */
                    display: flex;
                    align-items: center;
                    justify-content: center;
                
                    span {
                      margin-left: 15x;
                    }
                  }

                  button:hover {
                    background: white; /* Change le fond en blanc */
                    color: #4D00EF;
                  }
                
                  /* Spécifique pour l'état de chargement */
                  button.loading {
                    background: white; /* Change le fond en blanc */
                    color: #4D00EF; /* Change la couleur du texte en #4D00EF */
                    /* Si vous voulez aussi changer l'apparence de l'icône pendant le chargement, vous pouvez le faire ici */
                    /* par exemple, pour le faire tourner : */
                    display: flex;
                    justify-content: center;
                    align-items: center;
                  }
                  button.loading .icon {
                    animation: spin 1s linear infinite;
                    font-size: 15px;
                  }
                
                  /* Keyframes pour l'animation de rotation */
                  @keyframes spin {
                    0% { transform: rotate(0deg); }
                    100% { transform: rotate(360deg); }
                  }
                }                
                
            }
        }

        .container-step.fade {
            transition: opacity 0.5s;
          }
          
          .container-step.fade-entering {
            opacity: 0;
          }
          
          .container-step.fade-entered {
            opacity: 1;
          }
          
          .container-step.fade-exiting {
            opacity: 0;
          }
          
          

          .container-step.fade-entering {
            animation: slideOut 0.5s forwards;
          }
          
          .container-step.fade-entered {
            animation: slideIn 0.5s forwards;
          }
          
    }
}

@keyframes slideIn {
  0% {
    transform: translateX(100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slideOut {
  0% {
    transform: translateX(0);
    opacity: 1;
  }
  100% {
    transform: translateX(-100%);
    opacity: 0;
  }
}