.Profile {
    // width: calc(100% - 100px);
    width: 100%;
    padding: 0px 20px 0px 20px;
    margin-top: 120px;
    max-height: calc(100vh - 120px);
    overflow-y: auto;

    .container-profile-flex {
        display: flex;
        width: 100%;
        gap: 50px;
        padding-right: 20px;
        padding-bottom: 50px;

       .container-info-manager {
           display: flex;
           flex-direction: column;
           width: 15%;
           gap: 15px;

           img {
            width: 108px;
            height: 108px;
            border: 1px solid #4D00EF;
            border-radius: 5000px;
           }

           h3 {
            font-family: 'Plus Jakarta Sans';
            font-style: normal;
            font-weight: 700;
            font-size: 24px;
            line-height: 36px;
            color: #9548F7;
            width: 100%;
           }

           h4 {
            font-family: 'Plus Jakarta Sans';
            font-style: normal;
            font-weight: 700;
            font-size: 20px;
            line-height: 36px;
            color: #FFFFFF;            
           }

           span {
            font-family: 'Plus Jakarta Sans';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 24px;
            color: #959FA8;            
           }
       }

       .container-players-show {
           display: flex;
           flex-direction: column;
           gap: 15px;
           width: 100%;

           h4 {
            font-family: 'Plus Jakarta Sans';
            font-style: normal;
            font-weight: 700;
            font-size: 16px;
            line-height: 24px;
            color: #9A4AFF;
            // border-bottom: 1px solid #9A4AFF;
            padding: 10px;
           }

           .container-grid-card {
            width: 100%;
            display: grid;
            grid-template-columns: repeat(auto-fill, minmax(254px, 1fr));
            align-content: center;
            justify-content: center;
            gap: 2rem;

            .box-player::before {
                content: '';
                display: block;
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                background: rgba(0, 0, 0, 0.15);
                border-radius: 20px;
            }
            .box-player {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                border-radius: 20px;
                min-height: 350px;
                position: relative;
                // cursor: pointer;
                transition: all 3s;
            
                .box-top-card {
                    display: flex;
                    justify-content: space-between;
                    align-items: baseline;
                    padding: 10px 15px;
            
            
                    .flex-box {
                        display: flex;
                        flex-direction: column;
                        gap: 5px;
                    }
            
                    h2 {
                        font-family: 'Plus Jakarta Sans';
                        font-style: normal;
                        font-weight: 700;
                        font-size: 16px;
                        line-height: 20px;
                        text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.897);
                        /* identical to box height, or 188% */
                        color: #FFFFFF;
                        letter-spacing: 0.02em;
                        z-index: 1;
                    }
            
                    h3 {
                        font-family: 'Plus Jakarta Sans';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 30px;
                        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
                        z-index: 1;
                        color: #FFFFFF;
                        /* identical to box height, or 188% */
            
                        letter-spacing: 0.02em;
                    }
                }
            
                .box-bottom-card {
                    display: flex;
                    flex-direction: column;
                    gap: 10px;
                    background: rgba(0, 0, 0, 0.55);
                    border-radius: 20px;
                    padding: 15px 5px;
                    transition: all 3s;

                    // .show-more-button {
                    //     display: none;
                    // }

                    .show-more-button {
                        opacity: 0;           /* rend le bouton totalement transparent */
                        visibility: hidden;   /* masque le bouton */
                        height: 0;            /* supprime la hauteur du bouton */
                        overflow: hidden;     /* masque tout débordement */
                        margin-top: 0;        /* enlève l'espace entre le bouton et le contenu au-dessus */
                        transition: opacity 0.3s, visibility 0.3s, height 0.3s, margin-top 0.3s;  /* transition pour un effet doux */
                    }

                    .hidden {
                        opacity: 0;           /* rend le bouton totalement transparent */
                        visibility: hidden;   /* masque le bouton */
                        height: 0;            /* supprime la hauteur du bouton */
                        overflow: hidden;     /* masque tout débordement */
                        margin-top: 0;        /* enlève l'espace entre le bouton et le contenu au-dessus */
                        transition: opacity 0.3s, visibility 0.3s, height 0.3s, margin-top 0.3s;  /* transition pour un effet doux */
                    }
            
                    h4 {
                        font-family: 'Plus Jakarta Sans';
                        font-style: normal;
                        font-weight: 700;
                        font-size: 12px;
                        line-height: 20px;
                        letter-spacing: 0.02em;
                        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
                        color: #F5F7FA;
                        z-index: 1;
                    }
            
                    h5 {
                        z-index: 1;
                        font-family: 'Plus Jakarta Sans';
                        font-style: normal;
                        font-weight: 500;
                        font-size: 12px;
                        line-height: 15px;
                        text-align: right;
                        color: #F5F7FA;
                        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
                        opacity: 0.6;
                    }
            
                    .box-reputation-card {
                        display: flex;
                        justify-content: space-between;
                        // align-items: center;
                    }
                    
                    .align-box {
                        display: flex;
                        align-items: center;
                        flex-direction: column;
                    }
                    .box-content-card {
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
            
                    }
                }
            
            }

            .box-player:hover .box-bottom-card .show-more-button {
                font-family: 'Roboto Regular';
                font-style: normal;
                font-weight: 400;
                font-size: 16px;
                line-height: 19px;
                color: #FFFFFF;
                background: rgba(76, 0, 239, 0.993);
                border-radius: 15px;
                padding: 7px 0px;
                cursor: pointer;
                border: 0;
                display: block;
                z-index: 100;
                transition: all 0.3s;
                opacity: 1;           /* rend le bouton totalement visible */
                visibility: visible;  /* permet au bouton d'être vu */
                height: auto;         /* donne au bouton sa hauteur naturelle */
            }

            .box-player:hover .box-bottom-card .hidden {
                transition: all 0.3s;
                opacity: 1;           /* rend le bouton totalement visible */
                visibility: visible;  /* permet au bouton d'être vu */
                height: auto;         /* donne au bouton sa hauteur naturelle */
            }

            .box-player:hover .box-bottom-card .show-more-button:hover {
                background: #FFFFFF;
                color: rgba(76, 0, 239, 0.993);  /* optionnel: si vous souhaitez changer la couleur du texte en survol */
            }
            }
       }
    }
}